.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 4rem;
  background: linear-gradient(
    74deg,
    rgba(94, 39, 121, 1) 0%,
    rgba(134, 14, 41, 1) 100%
  );
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 10;
}

.header h1 {
  letter-spacing: 2.5px;
}

.main-image {
  width: 100%;
  height: 25rem;
  z-index: 0;
  overflow: hidden;
}

.main-image img {
  width: 110%;
  height: 100%;
  object-fit: cover;
  transform: rotateZ(4deg) translateY(-4rem) translateX(-1rem);
  border: 3px solid rgb(255, 255, 255);
}

@media screen and (min-width: 1000px) {
  .main-image {
    height: 30rem;
  }
}
