.cart-items {
  list-style: none;
  margin: 0;
  padding: 0;
  max-height: 20rem;
  overflow: auto;
}

.subtotal {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  font-size: 1rem;
  margin: 1rem 0;
}

.total {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  font-size: 1.5rem;
  margin: 1rem 0;
}

.actions {
  text-align: right;
}

.actions button {
  font: inherit;
  cursor: pointer;
  background-color: transparent;
  border: 1px solid #99120d;
  padding: 0.5rem 2rem;
  border-radius: 25px;
  margin-left: 1rem;
}

.actions button:hover,
.actions button:active {
  background-color: #c40840;
  border-color: #c40840;
  color: white;
}

.actions .button--alt {
  color: #b90a19;
}

.actions .button {
  background: rgb(228, 153, 60);
  background: linear-gradient(
    290deg,
    rgba(228, 153, 60, 1) 0%,
    rgba(205, 114, 75, 1) 100%
  );
  color: white;
}

.actions .button:hover {
  background: rgb(236, 117, 7);
  background: linear-gradient(
    266deg,
    rgba(236, 117, 7, 1) 0%,
    rgba(190, 50, 10, 1) 100%
  );
}
