.site-summary {
  text-align: center;
  max-width: 45rem;
  width: 90%;
  margin: auto;
  margin-top: -10rem;
  position: relative;
  background: rgb(222, 59, 16);
  background: linear-gradient(
    5deg,
    rgb(219, 72, 32) 0%,
    rgba(219, 133, 47, 1) 100%
  );
  color: white;
  border-radius: 20px;
  border: 2px solid rgb(255, 177, 61);
  padding: 2rem;
  box-shadow: 0 1px 18px 10px rgba(0, 0, 0, 0.25);
}

.site-summary h2 {
  font-size: 1.5rem;
  margin-top: 0;
}

.site-summary p {
  font-size: 14px;
  line-height: 25px;
}
