.form {
  text-align: right;
}

.form button {
  font: inherit;
  cursor: pointer;
  background-color: rgb(106, 179, 116);
  border: 2px solid rgb(38, 104, 65);
  color: white;
  padding: 0.15rem 1.5rem;
  border-radius: 20px;
  font-weight: bold;
}

.form button:hover,
.form button:active {
  background-color: rgb(130, 207, 110);
  border-color: rgb(26, 112, 30);
}
