}

html {
  font-family: "Poppins";
}

body {
  margin: 0;
  background: rgb(76, 38, 83);
  background: linear-gradient(
    173deg,
    rgba(76, 38, 83, 1) 0%,
    rgba(34, 26, 43, 1) 100%
