.cart-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid #d4b7e2;
  padding: 0.5rem 0;
  margin: 0.5rem 0;
}

.cart-item h2 {
  font-size: 18px;
  margin: 0 0 0.5rem 0;
  color: #363636;
}

.summary {
  width: 10rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.price {
  font-weight: bold;
  color: #34064e;
}

.amount {
  font-size: 14px;
  margin-left: 20px;
  font-weight: bold;
  border: 1px solid #ccc;
  padding: 0.25rem 0.75rem;
  border-radius: 6px;
  background: #642677;
  color: white;
}

.cart-actions {
  display: flex;
  flex-direction: row;
}

@media (min-width: 768px) {
  .actions {
    flex-direction: row;
  }
}

.cart-item-button {
  font: inherit;
  font-weight: bold;
  font-size: 1.25rem;
  color: #000000;
  border: 1px solid #030201;
  width: 3rem;
  text-align: center;
  border-radius: 6px;
  background-color: transparent;
  cursor: pointer;
  margin-left: 1rem;
  margin: 0.25rem;
}

.cart-item-button:hover,
.cart-item-button:active {
  background-color: #df9c0c;
  color: white;
}
