@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Poppins";
}

body {
  margin: 0;
  background: rgb(76, 38, 83);
  background: linear-gradient(
    173deg,
    rgba(76, 38, 83, 1) 0%,
    rgba(34, 26, 43, 1) 100%
  );
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 4rem;
  background: linear-gradient(
    74deg,
    rgba(94, 39, 121, 1) 0%,
    rgba(134, 14, 41, 1) 100%
  );
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 10;
}

.header h1 {
  letter-spacing: 2.5px;
}

.main-image {
  width: 100%;
  height: 25rem;
  z-index: 0;
  overflow: hidden;
}

.main-image img {
  width: 110%;
  height: 100%;
  object-fit: cover;
  -webkit-transform: rotateZ(4deg) translateY(-4rem) translateX(-1rem);
          transform: rotateZ(4deg) translateY(-4rem) translateX(-1rem);
  border: 3px solid rgb(255, 255, 255);
}

@media screen and (min-width: 1000px) {
  .main-image {
    height: 30rem;
  }
}

.cart-button {
  cursor: pointer;
  font: inherit;
  border: 1px solid rgb(233, 214, 180);
  background: rgb(228, 153, 60);
  background: linear-gradient(
    290deg,
    rgba(228, 153, 60, 1) 0%,
    rgba(205, 114, 75, 1) 100%
  );
  color: white;
  padding: 0.25rem 1rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-radius: 25px;
  font-weight: bold;
}

.cart-button:hover,
.cart-button:active {
  background-color: #2c0d00;
}

.icon {
  width: 1.35rem;
  height: 1.35rem;
  margin-right: 0.5rem;
}

.badge {
  background: linear-gradient(
    74deg,
    rgba(94, 39, 121, 1) 0%,
    rgb(134, 14, 84) 100%
  );
  padding: 0.05rem 1rem;
  border-radius: 25px;
  margin-left: 0.5rem;
  font-weight: bold;
  border: 1px solid rgb(240, 218, 193);
}

.cart-button:hover .badge,
.cart-button:active .badge {
  background-color: rgb(121, 20, 42);
}

.bump {
  -webkit-animation: bump 300ms ease-out;
          animation: bump 300ms ease-out;
}

@-webkit-keyframes bump {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  10% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
  30% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  50% {
    -webkit-transform: scale(1.15);
            transform: scale(1.15);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes bump {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  10% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
  30% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  50% {
    -webkit-transform: scale(1.15);
            transform: scale(1.15);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.site-summary {
  text-align: center;
  max-width: 45rem;
  width: 90%;
  margin: auto;
  margin-top: -10rem;
  position: relative;
  background: rgb(222, 59, 16);
  background: linear-gradient(
    5deg,
    rgb(219, 72, 32) 0%,
    rgba(219, 133, 47, 1) 100%
  );
  color: white;
  border-radius: 20px;
  border: 2px solid rgb(255, 177, 61);
  padding: 2rem;
  box-shadow: 0 1px 18px 10px rgba(0, 0, 0, 0.25);
}

.site-summary h2 {
  font-size: 1.5rem;
  margin-top: 0;
}

.site-summary p {
  font-size: 14px;
  line-height: 25px;
}

.meals {
  max-width: 60rem;
  width: 90%;
  margin: 2rem auto;
  -webkit-animation: meals-appear 0.75s ease-out forwards;
          animation: meals-appear 0.75s ease-out forwards;
}

.meals ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

@-webkit-keyframes meals-appear {
  from {
    opacity: 0;
    -webkit-transform: translateY(4rem);
            transform: translateY(4rem);
  }

  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes meals-appear {
  from {
    opacity: 0;
    -webkit-transform: translateY(4rem);
            transform: translateY(4rem);
  }

  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

.card {
  padding: 1rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  border-radius: 14px;
  background-color: white;
}

.meal {
  display: flex;
  justify-content: space-between;
  margin: 1rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid #ccc;
}

.meal h3 {
  margin: 0 0 0.25rem 0;
}

.description {
  font-style: italic;
}

.price {
  margin-top: 0.25rem;
  font-weight: bold;
  color: rgb(121, 12, 115);
  font-size: 1.25rem;
}

.form {
  text-align: right;
}

.form button {
  font: inherit;
  cursor: pointer;
  background-color: rgb(106, 179, 116);
  border: 2px solid rgb(38, 104, 65);
  color: white;
  padding: 0.15rem 1.5rem;
  border-radius: 20px;
  font-weight: bold;
}

.form button:hover,
.form button:active {
  background-color: rgb(130, 207, 110);
  border-color: rgb(26, 112, 30);
}

.input {
  display: flex;
  align-items: center;
  margin-bottom: 0.5rem;
}

.input label {
  font-weight: bold;
  margin-right: 1rem;
}

.input input {
  width: 3rem;
  border-radius: 5px;
  border: 1px solid #ccc;
  font: inherit;
  padding-left: 0.5rem;
  text-align: center;
}

.cart-items {
  list-style: none;
  margin: 0;
  padding: 0;
  max-height: 20rem;
  overflow: auto;
}

.subtotal {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  font-size: 1rem;
  margin: 1rem 0;
}

.total {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  font-size: 1.5rem;
  margin: 1rem 0;
}

.actions {
  text-align: right;
}

.actions button {
  font: inherit;
  cursor: pointer;
  background-color: transparent;
  border: 1px solid #99120d;
  padding: 0.5rem 2rem;
  border-radius: 25px;
  margin-left: 1rem;
}

.actions button:hover,
.actions button:active {
  background-color: #c40840;
  border-color: #c40840;
  color: white;
}

.actions .button--alt {
  color: #b90a19;
}

.actions .button {
  background: rgb(228, 153, 60);
  background: linear-gradient(
    290deg,
    rgba(228, 153, 60, 1) 0%,
    rgba(205, 114, 75, 1) 100%
  );
  color: white;
}

.actions .button:hover {
  background: rgb(236, 117, 7);
  background: linear-gradient(
    266deg,
    rgba(236, 117, 7, 1) 0%,
    rgba(190, 50, 10, 1) 100%
  );
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 20;
  background-color: rgba(0, 0, 0, 0.75);
}

.modal {
  position: fixed;
  top: 20vh;
  left: 5%;
  width: 90%;
  background-color: white;
  padding: 1rem;
  border-radius: 18px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 30;
  -webkit-animation: slide-down 300ms ease-out forwards;
          animation: slide-down 300ms ease-out forwards;
}

@media (min-width: 768px) {
  .modal {
    width: 40rem;
    left: calc(50% - 20rem);
  }
}

@-webkit-keyframes slide-down {
  from {
    opacity: 0;
    -webkit-transform: translateY(-3rem);
            transform: translateY(-3rem);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes slide-down {
  from {
    opacity: 0;
    -webkit-transform: translateY(-3rem);
            transform: translateY(-3rem);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

.cart-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid #d4b7e2;
  padding: 0.5rem 0;
  margin: 0.5rem 0;
}

.cart-item h2 {
  font-size: 18px;
  margin: 0 0 0.5rem 0;
  color: #363636;
}

.summary {
  width: 10rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.price {
  font-weight: bold;
  color: #34064e;
}

.amount {
  font-size: 14px;
  margin-left: 20px;
  font-weight: bold;
  border: 1px solid #ccc;
  padding: 0.25rem 0.75rem;
  border-radius: 6px;
  background: #642677;
  color: white;
}

.cart-actions {
  display: flex;
  flex-direction: row;
}

@media (min-width: 768px) {
  .actions {
    flex-direction: row;
  }
}

.cart-item-button {
  font: inherit;
  font-weight: bold;
  font-size: 1.25rem;
  color: #000000;
  border: 1px solid #030201;
  width: 3rem;
  text-align: center;
  border-radius: 6px;
  background-color: transparent;
  cursor: pointer;
  margin-left: 1rem;
  margin: 0.25rem;
}

.cart-item-button:hover,
.cart-item-button:active {
  background-color: #df9c0c;
  color: white;
}

