.cart-button {
  cursor: pointer;
  font: inherit;
  border: 1px solid rgb(233, 214, 180);
  background: rgb(228, 153, 60);
  background: linear-gradient(
    290deg,
    rgba(228, 153, 60, 1) 0%,
    rgba(205, 114, 75, 1) 100%
  );
  color: white;
  padding: 0.25rem 1rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-radius: 25px;
  font-weight: bold;
}

.cart-button:hover,
.cart-button:active {
  background-color: #2c0d00;
}

.icon {
  width: 1.35rem;
  height: 1.35rem;
  margin-right: 0.5rem;
}

.badge {
  background: linear-gradient(
    74deg,
    rgba(94, 39, 121, 1) 0%,
    rgb(134, 14, 84) 100%
  );
  padding: 0.05rem 1rem;
  border-radius: 25px;
  margin-left: 0.5rem;
  font-weight: bold;
  border: 1px solid rgb(240, 218, 193);
}

.cart-button:hover .badge,
.cart-button:active .badge {
  background-color: rgb(121, 20, 42);
}

.bump {
  animation: bump 300ms ease-out;
}

@keyframes bump {
  0% {
    transform: scale(1);
  }
  10% {
    transform: scale(0.9);
  }
  30% {
    transform: scale(1.1);
  }
  50% {
    transform: scale(1.15);
  }
  100% {
    transform: scale(1);
  }
}
